<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <Markdown class="title is-1" :input="pageTitle" tag="h1" />
          <Markdown class="subtitle is-4" :input="pageSubTitle" tag="h2" />
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">

          <Markdown :input="pageContent" />

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <ticket-offer-form @submitForm="proceedToPurchase" />
  </div>
</template>

<script>
import TicketOfferForm from "@/ticketing/views/TicketOfferForm";
import Markdown from "@/components/Markdown";
import CopyTicketOffer from "@/ticketing/components/CopyTicketOffer";
import ProfileService from "@/services/ProfileService";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "BuyTicketsPending",
  components: {
    Markdown,
    CopyTicketOffer,
    TicketOfferForm
  },
  mixins: [personNameMixin, markdownSubstitutionsMixin],
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    applicationQuestions() {
      return this.person.data;
    },
    ticketQuestions() {
      return this.person.data;
    },
    pageTitle() {
      return this.substituteContent(
        this.event.pageTicketOfferTitle,
        this.person,
        this.event
      );
    },
    pageSubTitle() {
      return this.substituteContent(
        this.event.pageTicketOfferSubtitle,
        this.person,
        this.event
      );
    },
    pageContent() {
      return this.substituteContent(
        this.event.pageTicketOfferContentMarkdown,
        this.person,
        this.event
      );
    }
  },

  methods: {
    proceedToPurchase() {
      ProfileService.createProfile({
        person: {
          firstName: this.person.firstName,
          lastName: this.person.lastName,
          email: this.person.email
        },
        profile: {
          ...this.applicationQuestions,
          ...this.ticketQuestions
        }
      })
        .then((personRes) => {
          console.log(personRes)
          TicketService.createTicketOffer(this.event.slug, {
            personId: personRes.data.person.id
          })
            .then((ticketOfferRes) => {
              console.log(ticketOfferRes)
              this.$router.push({
                name: "ticket-offer-purchase",
                params: {
                  eventSlug: this.event.slug,
                  id: ticketOfferRes.data.ticketOffer.id
                }
              });
              NProgress.done();
            })
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
